import * as THREE from 'three'
import loadTexture from '../../../lib3d/loader/textureLoader'
import anime from 'animejs'
import {getAb} from '../../../lib3d/math'

import starRound from '../assets/starRound.png'
import star from '../assets/star.png'

const opacity = 0.4
const opacitySub = 0.6

class Glimmer extends THREE.Object3D {
  constructor (color, showStar = false, hosPicUrl) {
    super()

    Promise.all([
      loadTexture(starRound),
      loadTexture(star),
      hosPicUrl ? loadTexture(hosPicUrl) : Promise.resolve()
    ]).then(([starRound, star, hosPic]) => {
      if (hosPic) {
        let materialHos = new THREE.MeshBasicMaterial({color: 0xffffff, opacity, map: hosPic, transparent: true, depthWrite: false})
        let geometryHos = new THREE.PlaneGeometry(3.2, 3.2, 1, 1)
        let hosPlane = new THREE.Mesh(geometryHos, materialHos)
        this.add(hosPlane)
      }

      let material = new THREE.MeshBasicMaterial({color, opacity, map: starRound, transparent: true, depthWrite: false})
      let geometry = new THREE.PlaneGeometry(3.6, 3.6, 1, 1)
      this.plane = new THREE.Mesh(geometry, material)

      let materialSub = new THREE.MeshBasicMaterial({color, opacity: opacitySub, map: starRound, transparent: true, depthWrite: false})
      let geometrySub = new THREE.PlaneGeometry(2, 2, 1, 1)
      this.planeSub = new THREE.Mesh(geometrySub, materialSub)

      this.add(this.plane)
      this.add(this.planeSub)

      this.planeSub.position.z = 0.1

      if (showStar) {
        var geometryStar = new THREE.PlaneGeometry(1.6, 1.6, 1, 1)
        var materialStar = new THREE.MeshBasicMaterial({map: star, transparent: true})
        var starPlane = new THREE.Mesh(geometryStar, materialStar)

        this.add(starPlane)
        starPlane.position.z = 0.2
      } else {
        let materialSub = new THREE.MeshBasicMaterial({color, map: starRound, transparent: true, depthWrite: false})
        let geometrySub = new THREE.PlaneGeometry(0.64, 0.64, 1, 1)
        let dotMesh = new THREE.Mesh(geometrySub, materialSub)

        this.add(dotMesh)
        dotMesh.position.z = 0.2
      }

      this.visible = false

      this.timerKey = setTimeout(this.animate, Math.random() * 2000)
    })
  }

  animate = () => {
    var coord = this.coord = {x: 0.2}
    let thred = 0.8
    // let [a, b] = getAb(0, opacity, thred, opacity)
    let [a1, b1] = getAb(thred, opacity, 1, 0)

    const duration = 2000

    anime({
      targets: coord,
      x: 1,
      easing: 'linear',
      duration,
      loop: true,
      begin: () => {
        this.visible = true
      },
      update: () => {
        this.plane.scale.x = coord.x
        this.plane.scale.y = coord.x
        if (coord.x < thred) {
          this.plane.material.opacity = opacity
        } else {
          this.plane.material.opacity = a1 * coord.x + b1
        }
      }
    })

    var coord2 = this.coord2 = {x: 0}
    let thred2 = 0.8
    // let [a2, b2] = getAb(0, opacitySub, thred2, opacitySub)
    let [a3, b3] = getAb(thred2, opacitySub, 1, 0)

    anime({
      targets: coord2,
      x: 1,
      easing: 'linear',
      duration,
      loop: true,
      update: () => {
        this.planeSub.scale.x = coord2.x
        this.planeSub.scale.y = coord2.x
        if (coord.x < thred) {
          this.planeSub.material.opacity = opacitySub
        } else {
          this.planeSub.material.opacity = a3 * coord.x + b3
        }
      }
    })
  }

  dispose = () => {
    anime.remove(this.coord)
    anime.remove(this.coord2)
    clearTimeout(this.timerKey)
  }
}

export default Glimmer
