import * as THREE from 'three'

function createTextureFrom (text, fontSize = 28, fillColor = '#60a2dc', lineGap = 6) {
  const padding = {
    left: 4,
    right: 4,
    top: 4,
    bottom: 4
  }

  // create image
  let canvas = document.createElement('canvas')
  let ctx = canvas.getContext('2d')

  let span = document.createElement('span')
  span.innerText = text
  span.style.fontFamily = 'Arial'
  span.style.fontSize = `${fontSize}px`
  span.style.display = 'inline-block'
  span.style.lineHeight = '1'
  document.body.appendChild(span)

  let lines = text.split('\n')
  let width = span.offsetWidth + padding.left + padding.right
  let height = span.offsetHeight + padding.top + padding.bottom + lineGap * (lines.length - 1)
  let lineHeight = span.offsetHeight / lines.length

  span.parentNode.removeChild(span)

  // let textMeasure = ctx.measureText(text)

  canvas.width = width
  canvas.height = height

  ctx.fillStyle = 'rgba(0,0,0,0.1)'
  ctx.fillRect(0, 0, canvas.width, canvas.height)

  ctx.font = `${fontSize}px Arial`
  ctx.fillStyle = fillColor

  // ctx.textAlign = 'center'
  ctx.textBaseline = 'top'

  let x = padding.left
  let y = padding.top + 2

  for (let i = 0; i < lines.length; i++) {
    ctx.fillText(lines[i], x, y + (i * lineHeight) + i * lineGap)
  }

  // document.body.appendChild(canvas)

  // canvas contents will be used for a texture
  let texture = new THREE.Texture(canvas)
  texture.needsUpdate = true
  return texture
}

class BillBoard extends THREE.Object3D {
  constructor (text, placement = 'bottom', option = {}) {
    super()

    let mapFromText = createTextureFrom(text, option.fontSize, option.fillColor, option.lineGap)
    var spriteMaterial = new THREE.SpriteMaterial({map: mapFromText, depthTest: true})
    var sprite = new THREE.Sprite(spriteMaterial)
    this.add(sprite)

    const textureScale = 20
    let width = mapFromText.image.width / textureScale
    let height = mapFromText.image.height / textureScale
    sprite.scale.set(width, height, 1)

    const delta = 1.46

    switch (placement) {
      case 'top':
        sprite.position.set(0, height / 2 + delta, 0)
        break
      case 'bottom':
        sprite.position.set(0, -height / 2 - delta, 0)
        break
      case 'left':
        sprite.position.set(-width / 2 - delta, 0, 0)
        break
      case 'right':
        sprite.position.set(width / 2 + delta, 0, 0)
        break
    }
    // sprite.position.set(0, 0, 1)
    // sprite.position.copy(pos)
  }

  dispose = () => {

  }
}

export default BillBoard
