import * as THREE from 'three'
import loadTexture from '../../../lib3d/loader/textureLoader'
import {getAb} from '../../../lib3d/math'

import anime from 'animejs'
import flash_white from '../assets/flash_white.png'

const meshWidth = 0.20
const meshHeight = 64 * meshWidth
const meshSegment = 4

const [a, b] = getAb(4, 4, 50, 18)

let COLOR_MATS = {}

// 曲线控制点，偏移量函数（手动调试出来）
function distFun (x) {
  let powParam = 1.8
  return Math.max(Math.min(Math.pow(x, powParam) * 18 / Math.pow(50, powParam), 18), 0.8)
}

function getMats (color) {
  if (!COLOR_MATS[color]) {
    COLOR_MATS[color] = {
      lightingMaterial: new THREE.MeshBasicMaterial({
        color: color,
        depthWrite: false,
        transparent: true
      }),
      tubeMaterial: new THREE.MeshBasicMaterial({
        color: color,
        opacity: 0.6,
        depthWrite: false,
        transparent: true
      })
    }
  }

  return COLOR_MATS[color]
}

class Flashing extends THREE.Object3D {
  constructor (v1, v2, direction = 1, color) {
    super()
    this.v1 = v1
    this.v2 = v2
    this.index = 0
    this.curves = []

    this.dir = new THREE.Vector3().subVectors(v2, v1).normalize()

    let distance = v1.distanceTo(v2)

    this.cross = new THREE.Vector3()
    this.cross.crossVectors(this.dir, new THREE.Vector3(0, 0, 1))
    this.cross.normalize()

    let {lightingMaterial, tubeMaterial} = getMats(color)

    loadTexture(flash_white).then((map) => {
      lightingMaterial.map = map

      this.curves.push(this.drawLine(distance, tubeMaterial))

      const loop = () => {
        this.timerKey = setTimeout(() => {
          this.emit(lightingMaterial)
          loop()
        }, Math.random() * 2000 + 2000)
      }

      loop()
    })
  }

  drawLine = (distance, tubeMaterial) => {
    let dot = this.dir.dot(new THREE.Vector3(1, 0, 0)) > 0 ? -1 : 1

    const hOffset = distFun(distance)
    const crossOffset = distFun(distance)

    let curve = new THREE.QuadraticBezierCurve3(
      this.v1,
      new THREE.Vector3((this.v1.x + this.v2.x) * 0.5, (this.v1.y + this.v2.y) * 0.5, hOffset).addScaledVector(this.cross, dot * crossOffset),
      this.v2
    )

    const seg = Math.round(Math.max(4, a * distance + b))
    let tubeGeometry = new THREE.TubeGeometry(curve, seg, 0.12, 4)

    let tubeMesh = new THREE.Mesh(tubeGeometry, tubeMaterial)

    this.add(tubeMesh)

    return {
      curve,
      curveLength: curve.getLength()
    }
  }

    emit = (lightingMaterial) => {
      this.index++

      const {curve, curveLength} = this.curves[this.index % this.curves.length]

      let planemesh = getPlane(lightingMaterial)

      const ratio = meshHeight / curveLength
      let coord = {x: 0}
      let delta = ratio / meshSegment
      const zBias = 0.01

      anime({
        targets: coord,
        x: 1 + ratio,
        easing: 'linear',
        duration: curveLength / 0.015,
        begin: () => {
          this.add(planemesh)
        },
        update: () => {
          let position = planemesh.geometry.attributes.position
          let index = 0
          let array = position.array

          let cur = coord.x + delta
          for (let i = 0; i < position.count / 2; i++) {
            cur = cur - delta

            const curveP = curve.getPoint(cur < 0 ? 0 : cur)

            array[index++] = curveP.x - this.cross.x * meshWidth
            array[index++] = curveP.y - this.cross.y * meshWidth
            array[index++] = curveP.z + zBias

            array[index++] = curveP.x + this.cross.x * meshWidth
            array[index++] = curveP.y + this.cross.y * meshWidth
            array[index++] = curveP.z + zBias
          }
          planemesh.geometry.attributes.position.needsUpdate = true
        },
        complete: () => {
          this.remove(planemesh)
          recyclePlane(planemesh)
        }
      })
    }

  dispose = () => {
    clearTimeout(this.timerKey)
  }
}

let cache = []
function getPlane (mat) {
  if (cache.length === 0) {
    let planeBufferGeometry = new THREE.PlaneBufferGeometry(meshWidth, meshHeight, 1, meshSegment)
    return new THREE.Mesh(planeBufferGeometry, mat)
  } else {
    let plane = cache.pop()
    plane.material = mat
    return plane
  }
}

function recyclePlane (obj) {
  cache.push(obj)
}

export default Flashing
