import * as THREE from 'three'
import CameraController from '../../lib3d/cameraController'
import Flashing from './instance/flashing'
import Glimmer from './instance/glimmer'
import BillBoard from './instance/bilBoard'
import { calcCenterPos, calcMapScale, convertGeoToPixel, findBoundBoxFromGeojson } from '../../lib3d/mercator'
import nanshaGeojson from './assets/nanshaGeojson.json'
import MapTexture from '../../lib3d/instance/mapTexture'
import { clearThree } from '../../lib3d/utils'

class ThreeJsMap {
  constructor (htmlRoot, width = 800, height = 800, hospitalData, option) {
    // 中国地图中心经纬度转网络墨卡托，用于移动转换后的地图到中心
    let boundBox = findBoundBoxFromGeojson(nanshaGeojson)

    const centerPos = calcCenterPos([boundBox.centLng, boundBox.centLat])

    // 缩放因子,将地图最后 适应 100x100 的网格坐标
    const mapScale = calcMapScale([boundBox.maxLng, boundBox.maxLat], [boundBox.centLng, boundBox.centLat])

    this.clock = new THREE.Clock()
    this.sceneMap = new THREE.Scene()

    this.scene = new THREE.Scene()

    // this.curMap = new MapArea(nanshaGeojson, mapScale, centerPos)
    // this.sceneMap.add(this.curMap)

    let map = new MapTexture(option.mapImage)
    this.sceneMap.add(map)

    this.cameraController = new CameraController(width, height, htmlRoot)

    this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true, precision: 'highp' })
    this.renderer.setPixelRatio(Math.max(window.devicePixelRatio, 2))
    this.renderer.setSize(width, height)
    htmlRoot.appendChild(this.renderer.domElement)

    // 位置偏移统一纠正
    const deltaX = 4, deltaY = -3

    let targetPos = convertGeoToPixel(hospitalData.target.coord, mapScale, centerPos)
    let targetVec = new THREE.Vector3(targetPos[0] + deltaX, targetPos[1] + deltaY, 0)

    let glimmer = new Glimmer(parseInt(hospitalData.target.color, 16), true)
    glimmer.position.copy(targetVec)
    this.scene.add(glimmer)
    glimmer.position.z = 8
    glimmer.scale.set(1.6, 1.6, 1)

    hospitalData.hospitals.forEach(v => {
      const color = parseInt(v.color, 16)
      let startPos = convertGeoToPixel(v.coord, mapScale, centerPos)
      let startVec = new THREE.Vector3(startPos[0] + deltaX, startPos[1] + deltaY, 0)

      let billboard = new BillBoard(v.name, v.placement, option.textStyle)
      this.scene.add(billboard)
      billboard.position.copy(startVec)
      billboard.position.z = 0.01

      this.scene.add(new Flashing(startVec, targetVec, 1, color))

      let glimmer = new Glimmer(color, false, option.hosImages[v.imageIndex])
      glimmer.position.copy(startVec)
      this.scene.add(glimmer)
      glimmer.position.z = 0.02
    })

    const animate = () => {
      const delta = this.clock.getDelta()

      this.cameraController.updateCamera(delta)

      let camera = this.cameraController.getCamera()

      this.renderer.autoClear = true
      this.renderer.render(this.sceneMap, camera)

      this.renderer.autoClear = false
      this.renderer.render(this.scene, camera)

      this.timerKey = window.requestAnimationFrame(animate)
    }

    animate()
  }

  resize = (width, height) => {
    this.cameraController.resizeCamera(width, height)
    this.renderer.setSize(width, height)
  }

  dispose = () => {
    clearThree(this.sceneMap)
    clearThree(this.scene)
    this.renderer.dispose()
    this.cameraController.dispose()
    window.cancelAnimationFrame(this.timerKey)
  }
}

export default ThreeJsMap
