import React from 'react'
import ReactDOM from 'react-dom'
import ThreeJsMap from './ThreeJsMap'
import hospitalData from './constant/hospitalData'

export default class Map extends React.Component {
  static defaultProps = {
    hospitalData,
    option: {
      textStyle: {
        fillColor: '#60a2dc',
        fontSize: 28
      },
      mapImage: 'https://cec-1256174840.cos.ap-shanghai.myqcloud.com/guangzhou_nanshaqu@x2.png',
      hosImages: [
        'https://cec-1256174840.cos.ap-shanghai.myqcloud.com/hostype1.png',
        'https://cec-1256174840.cos.ap-shanghai.myqcloud.com/hostype2.png',
        'https://cec-1256174840.cos.ap-shanghai.myqcloud.com/hostype3.png'
      ]
    }
  }

  componentDidMount () {
    const props = this.props
    let root = ReactDOM.findDOMNode(this)
    this.three = new ThreeJsMap(root, root.offsetWidth, root.offsetHeight, props.hospitalData, props.option)
  }

  componentWillUnmount () {
    this.three.dispose()
    this.three = null
  }

  render () {
    const props = this.props
    return (
      <div style={props.style} className={props.className} />
    )
  }
}
